@import (reference) 'common/color.less';
.evalution-box{margin:0 auto;margin-top:50px;}
.select{width:94px;height:24px;color:#797979}
.select option{color:#797979}
.green{color:#9cc382}
/*备注弹框开始*/
.remarks .popup{width:420px;height:504px;margin-left:-210px;margin-top:-252px;}
.remarks .popup .popupbox{height:422px;width:330px;padding:0 45px;padding-top:25px;}
.remarks .popup .popupbox p{line-height:25px;font-size:14px;}
.delete-icon{
  width:20px;
  height:20px;
  display:inline-block;
  background:url("../../bundle/img/icon.png") no-repeat;
  background-position:-542px -91px;
  vertical-align:middle;
  cursor:pointer;
}
.input-box{position:relative;width:326px;height:76px;padding:2px;}
.remarks .popup textarea{position:absolute;left:0;top:0;width:324px;height:72px;display:block;}
.input-box .count-num{position:absolute;bottom:0;right:3px;}
/*备注弹框结束*/